export function yearsToStr(age) {
  var txt, count;
  count = age % 100;

  if (count >= 5 && count <= 20) {
    txt = "";
  } else {
    count = count % 10;

    if (count == 1) {
      txt = "";
    } else if (count >= 2 && count <= 4) {
      txt = "";
    } else {
      txt = "";
    }
  }

  return age + " " + txt;
}